import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
import axios from 'axios'

Vue.use(VueRouter)

const checkLogin = (from, to, next) => {
  // session storage에 세션 키가 있는지 검사한다.
  if (store.getters.sessionKey != '' && store.getters.userid != '') {
    axios({ // 세션키가 유효한지 서버에 조회한다.
      method: 'get',
      url: store.getters.apiServer + '/php/sessions.php',
      params: {
        key: store.getters.sessionKey,
        id: store.getters.userid
      },
    }).then ((resp) => {
      if (resp.status == 200 && resp.data.state == 'Success') {
        next()
      } else {
        next('/login')  
      }
      
    }).catch(err => {
      console.error(err);
      next('/login')
    })
    return
  }  

  return next('/login')
}

const routes = [
  {
    path: '/',
    name: 'Home',
    // component: Home,
    beforeEnter: (from, to, next) => {
      if (store.getters.sessionKey == '' || store.getters.userid == '') {
        return next('/login')  
      }

      return next('/system')
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue')
  },
  {
    path: '/system',
    name: 'System',
    component: () => import(/* webpackChunkName: "system" */ '../views/System.vue'),
    beforeEnter: checkLogin
  },
  {
    path: '/download',
    name: 'Download',
    component: () => import(/* webpackChunkName: "download" */ '../views/Download.vue'),
    beforeEnter: checkLogin
  },
  {
    path: '/log',
    name: 'Log',
    component: () => import(/* webpackChunkName: "log" */ '../views/Log.vue'),
    beforeEnter: checkLogin
  },
  {
    path: '/user',
    name: 'User',
    component: () => import(/* webpackChunkName: "user" */ '../views/User.vue'),
    beforeEnter: checkLogin
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
