import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const defaultSessionKey = (sessionStorage['sessionKey'] != undefined) ? sessionStorage['sessionKey'] : ''
const defaultUserID = (sessionStorage['userid'] != undefined) ? sessionStorage['userid'] : ''
const defaultType = (sessionStorage['type'] != undefined) ? sessionStorage['type'] : '0'
const defaultCreated = (sessionStorage['created'] != undefined) ? sessionStorage['created'] : ''
const defaultLastLogin = (sessionStorage['lastLogin'] != undefined) ? sessionStorage['lastLogin'] : ''
// const API_SERVER = 'http://192.168.0.3'
const API_SERVER = ''
const CLIENT_VER = "00.04"

const store = new Vuex.Store({
  state: {
    session: {
      key: defaultSessionKey,
      userid: defaultUserID,
      type: defaultType,
      created: defaultCreated,
      lastLogin: defaultLastLogin
    },
    apiServer: API_SERVER,
    version: CLIENT_VER
  },
  getters: {
    sessionKey(state) {
      return state.session.key
    },
    userid(state) {
      return state.session.userid
    },
    userType(state) {
      switch(state.session.type) {
        case 1:
          return 'administrator'
        case 2:
          return 'manager'
      }
      return 'guest'
    },
    isAdmin(state) {
      return state.session.type == '1'
    },
    apiServer(state) {
      return state.apiServer
    },
    version(state) {
      return state.version
    }
  },
  mutations: {
    logout(state) {
      state.session.key = ''
      state.session.userid = ''
      state.session.defaultType = ''
      state.session.created = ''
      state.session.lastLogin = ''
      sessionStorage['sessionKey'] = ''
      sessionStorage['userid'] = ''
      sessionStorage['type'] = ''
      sessionStorage['created'] = ''
      sessionStorage['lastLogin'] = ''
    },
    login(state, payload) {
      sessionStorage['sessionKey'] = payload.sid
      sessionStorage['userid'] = payload.id
      sessionStorage['type'] = payload.type
      sessionStorage['created'] = payload.created
      sessionStorage['lastLogin'] = payload.lastLogin
      state.session.key = payload.sid
      state.session.userid = payload.id
      state.session.type = payload.type
      state.session.created = payload.created
      state.session.lastLogin = payload.lastLogin
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store